lg >= 1200

.hide-width,.hide-height,.hide-max-width,.hide-max-height - classes that allow for css width animations
*/

@media (max-width: 437px) {
  .hide-xxs, .hidden-xxs {
    display: none !important;
  }
  .hide-width-xxs{
    width:0 !important;overflow:hidden !important;
  }
